export default {
  name: 'App',
  data(){
    return{
        transitionName:'', isShow:true
    }
  },
  created(){
      
  },
  watch:{
    $route(to, from) {
      //to0tofrom,,
        if(to.meta.index > from.meta.index){
            this.transitionName = 'slide-right'
        }else{
            this.transitionName = 'slide-left'
        }
    }
  }
}



